import InfoCard from "../../components/general/infoCard"

const ContactCards = ({ className, address, phoneNumber, email, faxNumber, googleMapsLink }) => (
    <div className={`${className} space-y-4 w-full`}>
        {/*  border-4 border-orange-200 */}

        <InfoCard
            title="Address"
            info={address.trim()}
            href={googleMapsLink}
            linkClass="whitespace-pre-wrap"
            newTab={true}
        />

        {/* <InfoCard
            title="Phone Number"
            info={phoneNumber}
            href={`tel:${phoneNumber}`}
        /> */}

        <InfoCard title="">

            <div className="flex flex-row justify-between items-center">

                <span className="font-normal">Phone Number:</span>

                &nbsp;

                <a href={`tel:${phoneNumber}`} className="hover:underline">
                    {phoneNumber}
                </a>

            </div>

            <div className="flex flex-row justify-between items-center">

                <span className="font-normal">Fax Number:</span>
                
                &nbsp;
                
                <span>{faxNumber}</span>
            
            </div>

        </InfoCard>

        <InfoCard
            title="Email"
            info={email}
            href={`mailto:${email}`}
        />

        {/* <InfoCard title="Fax">
            {faxNumber}
        </InfoCard> */}

    </div>
)

export default ContactCards

