import business from "../../../business"

const Map = ({ className }) => (
    <div className={`${className} w-full flex`}>
        <div className="rounded-lg overflow-hidden inline-block mx-auto p-2 shadow-lg bg-orange-200 bg-opacity-50" style={{ backdropFilter: 'blur(10px)' }}>
            <div className="rounded-lg overflow-hidden block">
                <iframe src={business.interactiveMapLink} width="400" height="300" style="border:0;" allowfullscreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade"></iframe>
            </div>
        </div>
    </div>
)

export default Map

