import Titling from "../home/titling"
import info from './info.js'
import ContactCards from "./contactCards"
import business from "../../../business"
import SubTitling from "../../components/general/subtitling"
import Map from "../../components/general/map"
import HoursSection from "../../components/general/hoursSection"
import ContactForm from "../../components/forms/contactForm"
import Input from "../../components/general/input"
import WaitTimeCard from "../../components/forms/waitTimeCard"

const Contact = () => (
    <main>

        <Titling
            title={info.title}
            subtitle={info.subtitle}
            className=""
        />

        {/* <Form className="" /> */}

        <HoursSection className="" />

        <SubTitling
            title={info.contactInfo.title}
            // subtitle="Talk To Us Directly"
            className="mt-16 mb-8"
        />

        <ContactCards
            address={business.address}
            googleMapsLink={business.googleMapsLink}
            phoneNumber={business.phoneNumber}
            email={business.email}
            faxNumber={business.faxNumber}
            className="mb-16"
        />
        
        <SubTitling
            title={info.contactForm.title}
            subtitle={info.contactForm.subtitle}
            className="mt-16 mb-8"
        />

        <ContactForm className="" />

        <SubTitling
            subtitle={info.map.title}
            className="mt-16 mb-2"
        />

        <Map className="mx-auto" />

    </main>
)

export default Contact

