const SubTitling = ({ className, title, subtitle }) => (
    <div className={className}>

        <h3 className="text-3xl font-bold text-center -mb-2">
			{title}
        </h3>

        {subtitle &&
            <h4 className='text-gray-500 mt-2 text-md font-medium text-center'>
                {subtitle}
            </h4>
        }
        
    </div>
)

export default SubTitling

