import business from "../../../business"
import Divider from "./divider"
import InfoCard from "./infoCard"
import SubTitling from "./subtitling"

const HoursSection = ({ className }) => (

    <div className={`${className}`}>
        
        <SubTitling
            title="Store Hours"
            className="mt-16 mb-8"
        />

        <InfoCard>
            
            <div className="whitespace-pre">
                {business.hours.map((hour, index) => (
                    <Hour hour={hour} index={index} />
                ))}
            </div>

            {/* <div className="text-sm font-normal text-center mt-2 p-2 text-gray-500">
                {business.hoursNote}
            </div> */}

        </InfoCard>

    </div>
)

const Hour = ({ hour, index }) => (
    <div>
        {index > 0 &&
            <Divider className="my-2" /> 
        }
        <div className="flex flex-row">
            <div className="font-bold">
                {hour.day}:
            </div>

            <div className="font-medium ml-auto text-right">
                {hour.time}
                <br />
                {hour.note}
            </div>
        </div>
    </div>
)

export default HoursSection

